<template>
  <div class="ds-forgot-password">
    <dsf-mobile-nav-bar label=""></dsf-mobile-nav-bar>
    <section>
      <!---->
      <div v-if="step == 0" class="ds-forgot-password-box">
        <div class="title">{{title}}</div>
        <div class="tips"></div>
        <label class="input">
          <i class="beforeIcon iconfont icon-lianxidianhua"></i>
          <input v-model.trim="phoneNumber" maxlength="20" type="text" placeholder="">
        </label>
        <div class="input">
          <i class="beforeIcon iconfont icon-yanzhengma1"></i>
          <div class="input-bt" :class="[codeBtType]" @click.stop="getCode">
            {{ codeBtType != '' ? `${countdownTime}s` : '' }}
          </div>
          <input v-model.trim="phoneCode" maxlength="12" type="text" placeholder="">
        </div>
        <div class="error-tips" v-if="codeAuthResult && phoneCode"> <i class="iconfont icon-tixingshixin" />{{codeAuthResult}}</div>
        <van-button
          :disabled="!phoneNumber.length || !phoneCode.length"
          :loading="isWaiting"
          type="primary"
          block
          @click="nextStep(1)">
            
        </van-button>
      </div>
      <!---->
      <div v-if="step == 1" class="ds-forgot-password-box">
        <div class="title"></div>
        <div class="tips">{{notice}}</div>
        <label class="input">
          <i class="beforeIcon iconfont icon-password1"></i>
          <input type="password" v-model="newpwd" placeholder="">
        </label>
        <label class="input">
          <i class="beforeIcon iconfont icon-password1"></i>
          <input type="password" v-model="confirmNewpwd" placeholder="">
        </label>
        <van-button type="primary" :loading="isWaitingTwo" block @click="nextStep(2)" :disabled="!newpwd||(newpwd&&newpwd!==confirmNewpwd)"> </van-button>
      </div>
      <!---->
      <div v-if="step == 2" class="ds-forgot-password-box">
        <div class="title"></div>
        <div class="success-image">
          <i class="iconfont icon-duigou"></i>
        </div>
        <p class="success-tip"></p>
        <van-button type="primary" block @click="reLogin"> </van-button>
      </div>
    </section>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformForgotPassword",
  ctrlCaption: "",
  mixins: [$mixins.control],
  props: {
    title:{
      type:String,
      default:''
    },
    intervalUrl:{
      type:String,
      default:'oua2/user/pwd/send/interval'
    },
    getCodeUrl:{
      type:String,
      default:'oua2/user/pwd/send'
    },
    authCodeUrl:{
      type:String,
      default:'oua2/user/pwd/token'
    },
    alterPwdUrl:{
      type:String,
      default:'oua2/user/pwd/update'
    }
  },
  data() {
    return {
      // 
      step: 0,
      // 
      isWaiting: false,
      // countdown
      codeBtType: "",
      // 
      countdownTime: 0,
      countdownTimes:10,
      notice:'',
      // 
      phoneNumber: '',
      // 
      phoneCode: '',
      //token
      token:'',
      // 
      codeAuthResult:'',
      // 
      newpwd: "",
      //
      confirmNewpwd:'',
      // 
      isWaitingTwo:false,
    }
  },
  watch: {
    phoneCode(){
      this.codeAuthResult=''
    }
  },
  mounted() {
    // 
    this.getInterval();
  },
  methods: {
    getInterval(){
      this.$http
        .get(this.intervalUrl, {}, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            this.countdownTimes = data.interval;
            this.countdownTime = data.interval;
            this.notice = data.notice
          } else {
            dsf.layer.message(message || "", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "", false);
        })
        .always(() => {
        });
    },
    // 
    getCode() {
      if (this.codeBtType != "") return;
      if (this.phoneNumber === "") {
        dsf.layer.message("", false);
        return;
      }
      if (!dsf.validate("isMobileOrTelPhone", this.phoneNumber)) {
        dsf.layer.message("", false);
        return;
      }
      let loading = dsf.layer.loading();
      // todo setTimeout
      this.$http
        .post(this.getCodeUrl, { phone: this.phoneNumber }, $$webRoot.dsfa)
        .done(({ success, data, message }) => {
          if (success) {
            dsf.layer.message("");
            this.codeBtType = "countdown";
            dsf.layer.closeLoading(loading);
            this.doCountdown();
          } else {
            dsf.layer.message(message || "", false);
          }
        })
        .error((err) => {
          dsf.error(err);
          dsf.layer.message(err?.message || "", false);
        })
        .always(() => {});

    },
    // 
    doCountdown() {
      this.countdownTime = this.countdownTimes - 1;
      this.timeout = setInterval(() => {
        this.countdownTime--;
        if (this.countdownTime <= -1) {
          this.codeBtType = "";
          clearInterval(this.timeout);
        }
      }, 1000);
    },
    // 
    authCode(){
      return new Promise((resolve)=>{
        this.$http
          .get(this.authCodeUrl, { phone: this.phoneNumber,code:this.phoneCode }, $$webRoot.dsfa)
          .done(({ success, data }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:data||'',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'',token:null})
          })
          .always(() => {});
      })
     
    },
    // 
    async nextStep(step) {
      if(step===1){
        // 
        this.isWaiting = true;
        let {err,token} = await this.authCode()
        this.isWaiting = false;
        if(err){
          this.codeAuthResult = err
          return false
        }
        this.token = token
        this.step = step;
      }else if(step===2) {
        this.isWaitingTwo = true;
        let {err} = await this.alterPassword()
        this.isWaitingTwo = false;
        if(err) return dsf.layer.message(err, false);
        
        this.step = step;
      }
    },
    // 
    alterPassword(){
      return new Promise((resolve)=>{
        this.$http
          .post(this.alterPwdUrl, { phone: this.phoneNumber,token:this.token,pwd:this.newpwd}, $$webRoot.dsfa)
          .done(({ success, data, message }) => {
            if (success) {
              resolve({err:null,token:data})
            } else {
              resolve({err:message||'',token:null})
            }
          })
          .error((err) => {
            resolve({err:err?.message||'',token:null})
          })
          .always(() => {});
      })
    },
    reLogin() {
      this.$dispatch('complete')
      this.$router.go(-1)
    },
  }
})
</script>