import AlloyFinger from 'alloyfinger';
const dispatch = (target, eventName, data) => {
  if(dsf?.client?.type?.indexOf?.("IE") !== 0) {
    const event = new CustomEvent(eventName, { detail: data });
    target.dispatchEvent(event);
  }
}
export default function() {
  new AlloyFinger(document.body, {
    // 
    pointStart() {
      dispatch(document.body, 'pointStart');
    },
    // 
    multipointStart() {
      dispatch(document.body, 'multipointStart');
    },
    // 
    rotate(e) {
      dispatch(e.target, 'rotate', e);
    },
    // 
    pinch(e) {
      dispatch(e.target, 'pinch', e);
    },
    // 
    multipointEnd() {
      dispatch(document.body, 'multipointEnd');
    },
    // 
    pressMove(e) {
      dispatch(e.target, 'pressMove', e);
    },
    // 
    tap(e) {
      dispatch(e.target, 'tap', e);
    },
    // 
    doubleTap(e) {
      dispatch(e.target, 'doubleTap', e);
    },
    // 750ms
    longTap(e) {
      dispatch(e.target, 'longTap', e);
    },
    // 
    swipe(e) {
      dispatch(e.target, 'swipe', e);
    },
    // 
    singleTap(e) {
      dispatch(e.target, 'singleTap', e);
    }
  });
}