<template>
  <div
    class="ds-mobile-platform-card-item-by-PLTR-mode-three"
    :style="controlStyle"
    @click="_clickBefore(itemClick)"
  >
    <template v-if="isDesign">
      <div class="cover">
        <van-image
          src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
          fit="cover"
        ></van-image>
        <div class="cover-mask">05-29 {{ linkMark }} 02-28</div>
      </div>
      <div class="title-warp">
        <div class="title ds-multi-ellipsis-1">
          
          
          
          
          
        </div>
        <div class="study-time">
          <!--  -->
          <div class="Compulsory-time">{{ getPrefix("compulsoryTime") }} 20</div>
          <!--  -->
          <div class="Elective-time">{{ getPrefix("electiveTime") }} 58.5</div>
          <div class="bage">
            <van-image :src="defaultBage" fit="cover"></van-image>
          </div>
        </div>
        <div class="subtitle">{{ getPrefix("num") }} 3958</div>
      </div>
    </template>
    <template v-else>
      <div class="cover">
        <van-image :src="data['cover'] | imgFormat" fit="cover"></van-image>
        <div
          class="cover-mask"
          v-if="data['coursePeriod-sdate'] != null && data['coursePeriod-edate'] != null"
        >
          {{ data["coursePeriod-sdate"] }} {{ linkMark }} {{ data["coursePeriod-edate"] }}
        </div>
      </div>
      <div class="title-warp">
        <div class="title ds-multi-ellipsis-1">
          {{ data["title"] }}
        </div>
        <div class="study-time">
          <!--  -->
          <div class="Compulsory-time">
            {{ getPrefix("compulsoryTime") }} {{ data["compulsoryTime"] }}
          </div>
          <!--  -->
          <div class="Elective-time">
            {{ getPrefix("electiveTime") }} {{ data["electiveTime"] }}
          </div>
          <div class="bage">
            <slot name="bage" v-bind="data"></slot>
          </div>
        </div>
        <div class="subtitle">{{ getPrefix("num") }} {{ data["num"] }}</div>
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByPLTRModeThree",
  ctrlCaption: "",
  mixins: [$mixins.card],
  props: {
    //
    padding: {
      default: () => [10, 10, 10, 10],
    },
    //
    keysMap: {
      type: Array,
      default() {
        return [
          { from: "cover", to: "cover", type: "text", desc: "" },
          { from: "title", to: "title", type: "text", desc: "" },
          {
            from: "num",
            to: "num",
            type: "text|number",
            desc: "",
            prefix: "",
          },
          {
            from: "compulsoryTime",
            to: "compulsoryTime",
            type: "text|number",
            desc: "",
            prefix: "",
          },
          {
            from: "electiveTime",
            to: "electiveTime",
            type: "text|number",
            desc: "",
            prefix: "",
          }, //
          { from: "status", to: "status", type: "text", desc: "" },
          {
            from: "coursePeriod-sdate",
            to: "coursePeriod-sdate",
            type: "text|number",
            desc: "-",
          },
          {
            from: "coursePeriod-edate",
            to: "coursePeriod-edate",
            type: "text|number",
            desc: "-",
          },
        ];
      },
    },
    //
    lineNum: {
      type: Number,
      default: 2,
    },
    //
    detailUrl: {
      type: String,
      default: "",
    },
    //
    linkMark: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      defaultBage: require("../../../assets/styles/themes/img/pass.png"),
    };
  },
  computed: {
    controlStyle() {
      let { padding, isDesign } = this;
      let style = {
        padding: padding.join("px ") + "px",
      };
      // if (isDesign) {
      //   style.padding = "10px 10px 10px 10px";
      // }
      return style;
    },
  },
  methods: {
    itemClick() {
      if (this.$listeners.itemClick) {
        this.$dispatch("itemClick", this.local);
      } else if (this.detailUrl) {
        let url = this.$replace(this.detailUrl, this.local);
        window.location.href = dsf.url.getWebPath(url);
      }
    },
  },
});
</script>
