<template>
  <div
    class="ds-mobile-platform-card-item-by-PLTR-mode-one"
    :style="controlStyle"
    @click="_clickBefore(itemClick)"
  >
    <template v-if="isDesign">
      <div class="cover" :style="imgHeight">
        <van-image
          src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"
          fit="cover"
        ></van-image>
      </div>
      <div class="title-warp">
        <div class="title  ds-multi-ellipsis-2">
          
          
          
          
          
        </div>
        <div class="subtitle"> 3958</div>
      </div>
    </template>
    <template v-else>
      <div class="cover" :style="imgHeight">
        <van-image :src="data['cover'] | imgFormat" fit="cover"></van-image>
      </div>
      <div class="title-warp">
        <div class="title  ds-multi-ellipsis-2">
          {{ data["title"] }}
        </div>
        <div class="subtitle">{{ getPrefix("num") }} {{ data["num"] }}</div>
      </div>
    </template>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobilePlatformCardItemByPLTRModeOne",
  ctrlCaption: "",
  mixins: [$mixins.card],
  props: {
    //
    padding: {
      default: () => [10, 10, 10, 10],
    },
    //
    keysMap: {
      type: Array,
      default() {
        return [
          { from: "cover", to: "cover", type: "text", desc: "" },
          { from: "title", to: "title", type: "text", desc: "" },
          {
            from: "num",
            to: "num",
            type: "text",
            desc: "| ",
            prefix: "",
          },
        ];
      },
    },
    //
    picHeight: {
      type: String,
      default: "76.5",
    },
    //
    detailUrl: {
      type: String,
      default: ''
    },
  },
  computed: {
    controlStyle() {
      let { padding, isDesign } = this;
      let style = {
        padding: padding.join("px ") + "px",
      };
      // if (isDesign) {
      //   style.padding = "10px 10px 10px 10px";
      // }
      return style;
    },
    imgHeight() {
      return { height: this.picHeight+'px' };
    },
  },
  methods: {
    itemClick() {
      if (this.$listeners.itemClick) {
        this.$dispatch("itemClick", this.local);
      } else if (this.detailUrl) {
        let url = this.$replace(this.detailUrl, this.local);
        window.location.href = dsf.url.getWebPath(url);
      }
    },
  },
});
</script>
